
import React from 'react';
import {useState, useEffect, createContext, useContext} from "react";
import {Navbar, Container, Nav, NavDropdown, Form, FormControl, Button} from 'react-bootstrap';
import PropTypes from 'prop-types';

function NavigationBar({dark=false, brandName="Brand Name", navItems, }){
  if (navItems){
    navItems= navItems.map((v)=>{
     return <Nav.Link href="#">{v.lable}</Nav.Link>
    })
  }else{
    navItems=""
  }
    return(
    <Navbar bg={dark?'dark':'light'} variant={dark?'dark':'light'} expand="lg">
      <Container fluid>
        <Navbar.Brand href="#">{brandName}</Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />

        <Navbar.Collapse>
          <Nav className="me-auto my-2 my-lg-0" style={{ maxHeight: '100px' }} navbarScroll>
            <Nav.Link href="#">Home</Nav.Link>
            <Nav.Link href="#">Link</Nav.Link>
            {navItems}

            
            <NavDropdown title="Link">
              <NavDropdown.Item href="#">Action</NavDropdown.Item>
              <NavDropdown.Item href="#">Another action</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#">Something else here</NavDropdown.Item>
            </NavDropdown>
            {/* leftside */}
          </Nav>

          {/* rightside  */}
          <Form className="d-flex">
            <FormControl type="search" placeholder="Search" className="me-2" aria-label="Search"/>
            <Button variant="outline-success">Search</Button>
          </Form>
          
        </Navbar.Collapse>
      </Container>
    </Navbar>
    )
}

NavigationBar.propTypes={
  dark: PropTypes.bool,
  brandName: PropTypes.string,
  navItems: PropTypes.objectOf({lable:PropTypes.string})
}


export default NavigationBar